body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo{
   
    width: 80px;
    height: 22px;
    margin: auto;
    margin-left: 25px;
    margin-right: 0px;
   
    
   

}
.header{
    background-color: rgb(255, 255, 255);
    width: 100%;
    height: 55px;
    display: -webkit-flex;
    display: flex;
    
    box-shadow: -5px -9px 20px;
    
}
.searchbox{
    margin: auto;
    margin-left:50px;
    margin-right:0px;
}
.searchbox>input{
    height:25px;
    width:80%;
}
.searchbox>button{
    height:32px;
}
.account{
    margin: auto;
    margin-right: 25px;
    display: block;
    float:right;
   

}
.option{


    background-color: rgb(255, 255, 255);
  height: 25px;
  width: 25px;
  margin: auto;
  margin-left:10px;
  margin-right:0px;
   padding: 10px;
   border-radius: 100px;
   

}
.image-con{
    margin: auto;
    padding: 2px;
    
}
.optionlogo{
   width: 20px;
   
    
  
 

}
.option:hover{
    background-color: rgb(143, 143, 143);
}
.leftdiv{
    width: 18%;
    height: 100vh;
    overflow-y: hidden;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
 
   
    background-color: rgb(247, 247, 247);
    text-align: center
  
  
  }
  .leftdiv:hover{
    overflow-y: scroll;
  }

.tabs{
  font-size: 15px;
  padding: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
  text-decoration: none;
  

}
a{
  text-decoration: none;
}
.tabs:hover{
  background-color: rgb(214, 214, 214);
}


.contentcard{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    width: 220px;
    height: 200px;
    background-color: rgb(255, 255, 255);
    box-shadow: 1px 5px 30px -10px;
    margin: 3px;
    margin-top: 30px;
    overflow: hidden;
    font-size:small;
    
}
.thumbnail{
    width: 100%;
    
    
    background-color: aqua;
    overflow: hidden;
   

}
.thumbnail-image{
    
   
 
}
.content-title{
    margin: 5px;
    font-family:sans-serif;
    margin-bottom: 0px;
    color: rgb(66, 66, 66);
    font-size: 15px;
    
}
.content-owner{
    margin: 5px;
    -webkit-text-decoration-style:solid;
            text-decoration-style:solid;
    -webkit-text-decoration-style:initial;
            text-decoration-style:initial;
    
    margin-bottom: 5px;


}
.statics{
    margin: 5px;
    
    margin-bottom: 5px;
    text-justify: inter-cluster;

}
.contentrow{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-flow: row;
            flex-flow: row;
    margin-left: 100px;
    margin-bottom: 50px;
    height: calc(150px+20px);
    background-color: rgb(252, 249, 245);
    

}
.filtered{
    position:relative;
    margin: 0%;
    padding: 0%;
    height: 10px;
    margin-left:0px;
    font-size: 15px;
    color: rgb(42, 42, 42);

}
.rightdiv{
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    
    justify-self: auto;
    background-color: rgb(255, 255, 255);
  
}

.content{
    width: 850px;
   height: 450px;
   margin-left: 10px;
  
 background-color: rgb(247, 247, 247);
   
    
}

html ,body
{
    margin: 0%;
    padding: 0%;
}
.app-root{
        width: 100%;
        
        margin: 0;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-flow:column;
            flex-flow:column;
}
.app-main{
    display:-webkit-flex;
    display:flex;
    width: 100%;
   
    -webkit-flex-flow:row;
   
            flex-flow:row;
    margin-top:5px;
}
body{
    margin: 0%;
    padding: 0%;
}
